.hero__overlay {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  background-size: cover;
  background-repeat: no-repeat;
  max-width: 1440px;
  padding-top: 310px;
  padding-bottom: 354px;

  @include mq(mobile) {
    max-width: 768px;
    padding-top: 190px;
    padding-bottom: 484px;
    background-position: center;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../images/hero/hero_sm.png);
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      -webkit-image-set(url(../images/hero/hero_sm.webp) 1x);

    @include mq(retina) {
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../images/hero/hero_sm@2x.png);
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        -webkit-image-set(url(../images/hero/hero_sm.png) 2x);
    }
  }

  @include mq(tablet) {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    background-position: right;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../images/hero/hero_lg.png);
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      -webkit-image-set(url(../images/hero/hero_lg.webp) 1x);

    @include mq(retina) {
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../images/hero/hero_lg@2x.png);
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        -webkit-image-set(url(../images/hero/hero_lg.png) 2x);
    }
  }

  @include mq(desktop) {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    background-position: center;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url(../images/hero/hero_lg.png);
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      -webkit-image-set(url(../images/hero/hero_lg.webp) 1x);

    @include mq(retina) {
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        url(../images/hero/hero_lg@2x.png);
      background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
        -webkit-image-set(url(../images/hero/hero_lg.png) 2x);
    }
  }
}

.hero__wrapper-txt {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.hero__wrapper-title {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @include mq(tablet) {
    flex-direction: row;
  }
  @include mq(desktop) {
    margin-bottom: 30px;
  }
}

.hero__title {
  font-weight: 400;
  font-size: 32px;
  line-height: 1.5;
  color: $secondary-txt;
  @include mq(tablet) {
    font-size: 32px;
    line-height: 1.04;
    margin-right: 10px;
  }
  @include mq(desktop) {
    font-size: 48px;
  }
}

.hero__title-txt {
  font-weight: 400;
  font-size: 32px;
  line-height: 1.5;
  color: $secondary-txt;
  margin-left: auto;

  @include mq(tablet) {
    font-size: 32px;
    line-height: 1.04;
    margin-left: 0;
  }

  @include mq(desktop) {
    font-size: 48px;
  }
}

.hero__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.45;
  color: $secondary-txt;
  width: 100%;
  padding: 12px 0;
  text-align: center;
  border: 1px solid $secondary-txt;
  transition: background-color 250ms $cubic;

  &:hover {
    background-color: $background-hover-color;
    border: 1px solid $background-hover-color;
  }

  &:focus {
    background-color: $background-pressed-color;
    border: 1px solid $background-pressed-color;
  }

  @include mq(tablet) {
    width: 194px;
  }
  @include mq(desktop) {
    font-size: 22px;
    width: 194px;
  }
}

.scramble-text {
  font-weight: 400;
  font-size: 32px;
  line-height: 1.5;
  color: $secondary-txt;
  text-align: center;

  @include mq(tablet) {
    font-size: 32px;
    line-height: 1.04;
  }
  @include mq(desktop) {
    font-size: 48px;

    margin-bottom: 30px;
  }
}

.dud-text {
  color: $secondary-txt;
}

.btn-up {
  position: fixed;
  right: 20px;
  bottom: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  z-index: 30;
}

.btn-up::before {
  content: '';
  width: 40px;
  height: 40px;
  background: $background-main no-repeat center center;
  border-radius: 50%;
  background-size: 100% 100%;
  background-image: url('../images/icons/up.svg');

  @include mq(desktop) {
    width: 60px;
    height: 60px;
  }
}

.btn-up_hide {
  display: none;
}


