.usage {
  @include mq(desktop) {
    padding-top: 151px;
  }
}

.usage__wrapper {
  display: flex;
  flex-direction: column;

  @include mq(desktop) {
    flex-direction: row;
  }
}

.usage__txt {
  font-weight: 400;
  font-size: 24px;
  margin-bottom: 30px;

  @include mq(desktop) {
    font-size: 30px;
    line-height: 1.46;
    margin-bottom: 0;

    &:not(:last-child) {
      margin-bottom: 78px;
    }
  }
}

.usage__info-wrapper {
  @include mq(desktop) {
    width: 500px;
    margin-right: 140px;
  }
}
.usage__video-wrapper {
  @include mq(tablet-only) {
    display: flex;
    justify-content: center;
  }
}
.usage__video {
  background-color: #d9d9d9;
  outline: $pressed-txt;
  width: 320px;
  @include mq(desktop) {
    width: 400px;
  }
}
