@import './mobile-menu';

.header {
  position: absolute;
  width: 100%;
  z-index: 10;
  display: flex;
  padding-top: 25px;
  padding-bottom: 25px;

  @include mq(desktop) {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}

.header-contract {
  background-color: $background-secondary;
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
}

.header__container {
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: space-between;
  @include mq(desktop) {
    align-items: center;
  }
}

.header__logo {
  font-weight: 400;
  font-size: 24px;
  line-height: 1.45;
  color: $secondary-txt;

  @include mq(desktop) {
    font-weight: 400;
    font-size: 30px;
    line-height: 1.46;

    margin-right: 274px;
  }
}

.logo-contract{
  font-weight: 400;
    font-size: 24px;
    line-height: 1.45;
    color: $secondary-txt;
    transition: color 250ms $cubic;
  
    @include mq(desktop) {
      font-weight: 400;
      font-size: 30px;
      line-height: 1.46;
  
      margin-right: 274px;
    }

    &:hover{
      color:$accent-txt;
    }
    &:focus{
      color:$pressed-txt;
    }
}

.nav {
  display: none;
  @include mq(desktop) {
    display: flex;
    margin-right: 165px;
  }
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.nav__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.45;
  color: $secondary-txt;
  transition: color 250ms $cubic;
  &:hover {
    color: $accent-txt;
  }
  &:focus {
    color: $pressed-txt;
  }
}

.header__link {
  display: none;

  @include mq(desktop) {
    display: block;
    font-weight: 400;
    font-size: 22px;
    line-height: 1.45;
    color: $secondary-txt;
    padding: 9px 20px;
    border: 1px solid $background-main;
    transition: background-color 250ms $cubic;
    margin-left: auto;

    &:hover {
      background-color: $background-hover-color;
      border: 1px solid $background-hover-color;
    }
  }
}

.mobile__menu-open {
  display: flex;
  margin-left: auto;
  padding: 0;
  outline: none;
  align-items: center;
  @include mq(desktop) {
    display: none;
  }
}
